body
{
    font-family: 'Montserrat';
}
.panda-block
{
    background: url('../img/panda.png') no-repeat center center;
    background-size: cover;
}
.nav-link
{
    font-family: 'Montserrat';
    font-size: 24px;
    font-weight: 400;

    letter-spacing: -.03px;
    text-transform: lowercase;

    color: #303439;
}
.fragmanu-block
{
    background: url('../img/fragmanu.png') no-repeat center center;
    background-size: cover;
}
.cuccina-block
{
    background: url('../img/Cucina.png') no-repeat center center;
    background-size: cover;
}
.fit-block
{
    background: url('../img/fit4mom.png') no-repeat center center;
    background-size: cover;
}
.zdorova-block
{
    background: url('../img/zdory.png') no-repeat center center;
    background-size: cover;
}
.apexhookah-block
{
    background: url('../img/apexhookah.png') no-repeat center center;
    background-size: cover;
}
.padding-block
{
    padding: 155px;
    padding-left: 75px;
}
.button-rounder
{
    width: 222px;
    height: 43px;

    cursor: pointer;

    border: 1px solid rgba(32, 32, 32, .5);
    border-radius: 22px;
    background: transparent;
}
.button-rounder-white
{
    width: 222px;
    height: 43px;

    cursor: pointer;

    border: 1px solid rgba(32, 32, 32, .5);
    border-color: white;
    border-radius: 22px;
    background: transparent;
}

.contact-text
{
    font-family: 'Montserrat';
    font-size: 30px;
    font-weight: 400;

    letter-spacing: -.4px;

    color: #303439;
}
.contact-title
{
    font-family: 'Montserrat';
    font-size: 18px;
    font-weight: 400;

    letter-spacing: -.03px;
    text-transform: uppercase;

    color: #798ea5;
}

.footer-text
{
    font-family: 'Montserrat Light';
    font-size: 18px;
    font-weight: 300;

    letter-spacing: -.03px;

    opacity: .5;
    color: #303439;
}
